import React from "react"

import { Grid, PageHeader } from "antd"

import AppLogo from "../assets/logo.png"

const { useBreakpoint } = Grid

function Navbar(props: { extraComponents?: React.ReactNode[]; centerComponent?: React.ReactNode; target?: string }) {
  const { md } = useBreakpoint()

  return (
    <PageHeader
      title={
        <a href={"/"} style={{ color: "white" }} target={props.target || ""}>
          Lazy Vaccine Platform
        </a>
      }
      avatar={{ gap: 0, src: AppLogo, size: 48 }}
      extra={md ? props.extraComponents : <></>}
    >
      {md ? props.centerComponent : <></>}
    </PageHeader>
  )
}

export default Navbar
